/*
 *  Testimonials
 *    Styles for the testimonials section
 *
 *  Version:
 *    1.0.0 - 2021/10/21
 */

.c-testimonials {
  padding: 5rem 0 5.1875rem;
  background-color: $s-color--background-subdued;

  // Header
  &__header {
    padding: 0 $s-gutter--edge-mobile;
  }

  // Title
  &__title {
    margin-bottom: 0.75rem;
  }

  // Testimonials
  &__testimonials {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding-left: 0.75rem;
    padding-bottom: 4rem;
    margin-top: 2rem;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-snap-type: x mandatory;
    cursor: grab;
    user-select: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    &.is-active {
      cursor: grabbing;
    };
  }

  // Testimonial Item
  &__testimonial {
    margin-right: 1.75rem;

    &:last-child {
      margin-right: $s-gutter--edge-mobile;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__testimonial {
      margin-right: 3.125rem;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 7.5rem 0 2.5625rem 0;

    &__header {
      padding: 0 #{'min(10%, 12.5rem)'};
      text-align: center;
    }

    &__testimonials {
      padding: 0 #{'min(10%, 12.5rem)'} 4rem;
      margin-top: 2.55rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 12.5rem 0 10.375rem;

    &__inner {
      max-width: $s-page-wrap--large;
      margin: 0 auto;
    }

    &__title {
      margin-bottom: 1.5rem;
    }

    &__testimonials {
      padding: 0 0 4rem;
      margin-top: 5.25rem;
      justify-items: center;
      overflow: visible;
      cursor: initial;
      user-select: initial;
    }

    &__testimonial, &__testimonial:last-child {
      margin-right: 0;
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__testimonials {
      column-gap: 5rem;
    }
  }
}
