/*
 *  Testimonial item object
 *    Styles for the testimonial item snippet.
 *
 *  Version:
 *    1.0.0 - 2021/10/21
 */

.o-testimonial-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 16.25rem;
  min-height: 8.5rem;
  background-image: url(../../assets/svg.shape-5.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
  user-select: none;

  &:nth-child(even) {
    background-image: url(../../assets/svg.shape-7.svg);
  }

  // Container
  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2.75rem 2.625rem 2.125rem;
    text-align: center;
  }

  // Content
  &__content {
    margin-bottom: 0.5rem;
    line-height: 1.225;
  }

  // Author
  &__author {
    margin-bottom: 0.5625rem;
    color: $s-color--font-focus;
  }

  // CTA
  &__cta-container {
    &.is-desktop { display: none; }
  }

  // Flag
  &__flag {
    &-wrapper {
      @include t-absolute-center-x;
      bottom: -1.6875rem;
      display: block;
      width: 4rem;
      padding: 0.375rem;
      background: url(../../assets/svg.shape-6.svg) no-repeat center center/100%;
    }

    &-container {
      padding-bottom: 88.23529411764706%;
    }

    &-image {
      mask-image: url(../../assets/svg.shape-6.svg);
      mask-repeat: no-repeat;
      mask-size: 100%;
      mask-position: center;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    width: 21.1875rem;
    min-height: 19.5rem;

    &:hover &__cta-container {
      opacity: 1;
      pointer-events: initial;
    }

    &:nth-child(even) {
      .o-testimonial-item__cta-container {
        background-image: url(../../assets/svg.shape-7.svg);
      }
    }

    &__inner{
      height: 100%;
    }

    &__container {
      padding: 3.875rem 3.75rem;
      height: 100%;
    }

    &__content {
      margin-bottom: 1.5625rem;
      line-height: 1.33;
    }

    &__cta {
      @include t-font--heading-6-laptop;

      &-container {
        @include t-anim--transition(opacity, $ease: linear);
        position: absolute;
        top: 0;
        left: 0;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        background-image: url(../../assets/svg.shape-5.svg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 100%;
        opacity: 0;
        pointer-events: none;

        &.is-mobile { display: none; }
        &.is-desktop { display: flex; }
      }
    }

    &__flag {
      &-wrapper {
        bottom: -0.9375rem;
        width: 4.6875rem;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__cta {
      @include t-font--heading-6-desktop;
    }
  }
}
